html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h3 {
  font-size: 2rem;
  color: #0078B3;
}

.navbar {
  display: flex;
  height: 236px;
}
.navbar .navbar__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.navbar .navbar__list .list__item {
  margin: 1rem 0;
  display: flex;
  flex-direction: row-reverse;
}
.navbar .navbar__list .list__item:first-child {
  margin-top: 0;
}
.navbar .navbar__list .list__item:last-child {
  margin-bottom: 0;
}
.navbar .navbar__list .list__item .list__link {
  margin-left: 0.81rem;
  text-decoration: none;
  color: #0078B3;
  line-height: 1.25rem;
  font-size: 0.875rem;
  font-weight: bold;
}
.navbar .navbar__list .list__bar {
  width: 0.25rem;
  border-radius: 14px;
  color: black;
  background-color: #0D1C3D;
  margin-left: -0.25rem;
}
.navbar .navbar__list .list__bar--active {
  background-color: #0D1C3D;
}
.navbar .navbar__bar {
  width: 0.25rem;
  border-radius: 14px;
  color: black;
  background-color: #E9EBEE;
}